export default {
    props: {
        hideRightArrow: {
            type: Boolean,
            default: false
        },
        placeholder: {
            type: String,
            default: '请选择'
        },
        items: {
            type: Array,
            default: [{
                label: 'label1',
                value: ''
            }]
        }
    },
    data() {
        return {
            current: 0
        }
    },
    methods: {
        clickItem(index) {
            this.current = index;
            console.log(this.items[index])
            this.$emit('change', this.items[index])
        }, 
    },
}
